<template>

    <div>
        <v-toolbar flat color="none">

            <v-app-bar-nav-icon class="ma-3" v-on:click="$router.push('/users')">
                <v-icon large>navigate_before </v-icon> <span>Назад</span>
            </v-app-bar-nav-icon>
            <v-spacer></v-spacer>


        </v-toolbar>
        <v-layout row wrap align-content-center  >


            <v-flex xs12 sm12 md6  class="pa-3">
                            <v-card flat >
                                <v-layout row >
                                    <v-row justify="center" sm6 >
                                        <v-avatar size="150" class="ma-3">
                                            <v-img v-if="user.image" :src="user.image"></v-img>
                                            <v-img v-else :src="require('@/assets/default.png')"></v-img>

                                        </v-avatar>
                                    </v-row>
                                    <v-flex sm6 align-self-center>
                                        <div>
                                            <div class="title black--text " >{{ user.first_name}} {{ user.last_name}}</div>
                                            <div class="subtitle-2">{{user.position}}</div>
                                            <div class="subtitle-2">{{user.email}}</div>
                                        </div>
                                    </v-flex>


                                </v-layout>

                                <v-flex >
                                    <v-card-text >
                                        <div class="subtitle-2"> О себе:
                                            <v-card-text>
                                                {{user.profile}}
                                            </v-card-text>
                                        </div>
                                    </v-card-text>
                                </v-flex>

                                <v-layout row wrap class="text-center ma-3">
                                    <v-flex xs4 >
                                        <div>Баллы Спасибо</div>
                                        <v-progress-circular
                                                :rotate="-90"
                                                :size="100"
                                                :width="15"
                                                :value="10*user.share_points"
                                                color="primary"
                                        >
                                            {{ user.share_points + "/10" }}
                                        </v-progress-circular>
                                    </v-flex>

                                    <v-flex xs4 >
                                        <div> Рейтинг </div>
                                        <v-icon size="80" color="orange">emoji_events</v-icon>

                                        <div class="subtitle-2 orange--text"  > {{placelabel()}}</div>
                                    </v-flex >

                                    <v-flex xs4>
                                        <div> Личные баллы </div>

                                        <v-col align-self="center" class="font-weight-black red--text display-3 mt-3"  > {{user.personal_points}}</v-col>


                                    </v-flex >


                                </v-layout>



                            </v-card>
            </v-flex>
            <v-flex xs12 sm12 md6 >
                <v-card flat>
                    <div class="text-center ma-3 title"> Бейджи</div>
                    <div v-if="interest_list.length === 0">
                        <div class="text-center ma-6">
                            Данный пользователей не выбрал для себя бейджей
                        </div>
                    </div>
                    <v-layout row class="text-center ma-3">

                        <v-flex xs3 v-for="interest in friend_interests()" :key="interest" >
                            <v-icon class="ma-3"  size="50" color="secondary">
                                {{interest.badge}}</v-icon>

                            <div class="grey--text caption mb-6"> {{interest.text}}</div>


                        </v-flex>
                    </v-layout>
                </v-card>
                <v-divider></v-divider>
                <div class="mt-3">
                    <v-layout row wrap class="text-center" justify-space-around>
                        <SharePointsDialog :item="user"></SharePointsDialog>
                        <div>
                            <v-btn target="_blank" rel="noopener noreferrer" :href=malitoGen() dark color="indigo">Связаться</v-btn>
                        </div>
                    </v-layout>
                </div>
            </v-flex>

        </v-layout>
    </div>



</template>

<script>
    import SharePointsDialog from "./SharePointsDialog";
    export default {
        name: "UserPage",
        components: {SharePointsDialog},
        data:()=>({
            interest_list:[],
         }),

        created() {
            this.$store.dispatch("get_interests", this.user.id).then(response=>{
                this.interest_list = response.data
            })
        },

        computed: {
            user() {
                return this.$store.getters.users_list.find(user => user.id == this.$route.params.id)
            },
            badges(){
                return this.$store.getters.badges
            },

        },
        methods:{
            placelabel(){
                let len = this.$store.getters.users_list.length
                let place = this.$store.getters.users_list.findIndex(ex=> this.user ===ex)+1
                return place + '/'+ len
            },
            malitoGen(){
                return  "https://mail.google.com/mail/?view=cm&fs=1&to=" +this.user.email

            },
            friend_interests(){

                return this.badges.filter(b => this.interest_list.includes(b.badge))

            }
        }
    }
</script>

