<script>
    import myUpload from 'vue-image-crop-upload';
    import EditUser from "./EditUser";
    export default {
        name: "Main",
        components: {EditUser, myUpload},
        data:()=>({
            dialog:false,
            uploader: false,
            interest_list:'',
        }),
        beforeCreate() {
            this.$store.dispatch("getallusers");
            this.$store.dispatch("get_all_categories");
            this.$store.dispatch("get_transactions");

        },


        computed: {


            badges(){
                return this.$store.getters.badges
            },
            users_list(){
                return this.$store.getters.users_list
            },
            categories(){
                return this.$store.getters.categories
            },
            user(){
                return this.$store.getters.logged_user
            },
            transactions_from_me(){
                return this.$store.getters.transactions_from_me
            },
            transactions_to_me(){
                return this.$store.getters.transactions_to_me
            },
            avatar () {
                if (!this.user.image) {
                    return require('@/assets/default.png')
                }
                return this.user.image
            },



        },

        methods:{

            cropSuccess(imgDataUrl, field){
                console.log('-------- crop success --------');
                // this.imgDataUrl = imgDataUrl;
                console.log(field)
                console.log(imgDataUrl)
                function urltoFile(url, filename, mimeType){
                    return (fetch(url)
                            .then(function(res){return res.arrayBuffer();})
                            .then(function(buf){return new File([buf], filename,{type:mimeType});})
                    );
                }

                urltoFile(imgDataUrl, this.user.id+'.png', 'image/png').then(file =>{
                    const fd = new FormData()
                    fd.append('image', file);
                    this.$store.dispatch('edit_user', fd)
                    console.log(file);});


            },


            refresh() {
                this.$store.dispatch("refreshToken").then(response => {
                        console.log(response)
                    }
                )
            },
            get_user_by_id(id){
                let user = this.users_list.find(user=> user.id === id);
                return user.first_name + " " + user.last_name
            },
            get_user_img(id){
                let user = this.users_list.find(user=> user.id === id);
                return user.image
            },
            get_cat_by_id(id){
                let cat = this.categories.find(cat=> cat.id === id);
                return cat.name
            },
            reload(){
                console.log('1')
            },

            interests(){
                this.$store.dispatch("get_interests", this.$store.getters.logged_user.id).then( (response)=>{
                    this.interest_list =  response.data
                })
            },
            placelabel(){
                let len = this.$store.getters.users_list.length
                let place = this.$store.getters.users_list.findIndex(ex=> this.user.id ===ex.id)+1
                return place + '/'+ len
            },
            add_interest(interest){
                this.$store.dispatch('add_interest', interest).then( response=>{
                    if (response.data === 'excluded'){
                        let index = this.interest_list.findIndex(b => b === interest.badge)
                        this.interest_list.splice(index, 1)
                        }
                    else this.interest_list.push(interest.badge)
                }

                )
            },
            getColor(badge){
                if (this.interest_list.includes(badge)){
                    return 'primary'
                }
                else return 'grey darken-1'
            }


        }
    }





</script>


